// categories

.categories {
  font-family: "Arial", sans-serif;
  line-height: 1.3;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    line-height: 1.5;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    .centered();
  }
}

.categories__content {
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 20px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 31px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
    padding-top: 79px;
    padding-right: 110px;
    padding-left: 110px;
  }
}

.categories__card {
  margin-bottom: 23px;

  @media (min-width: @tablet-width) {
    max-width: 910px;
    margin-bottom: 31px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
    width: 46.8%;
  }
}

.categories__card:last-child {
  margin-bottom: 0;
}
