// header

.header {
  font-size: 0.875em;
  line-height: 1.28;

  @media (min-width: @tablet-width) {
    font-size: 1em;
    line-height: 1.5;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    width: 1440px;
    .centered();

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      width: 50%;
      height: 100%;
      background-color: @page-green;
      background-image: url("../img/index-background-desktop.jpg");
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 720px 696px;
    }

    &--inner {
      &::before {
        display: none;
      }
    }
  }
}

.header__content {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-top: 24px;
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 53px;
    padding-right: 110px;
    padding-left: 110px;
    font-size: 1.115em;
    line-height: 1.24;
  }
}

.header__head {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
  border-bottom: 1px solid @page-dark-gray;

  @media (min-width: @tablet-width) {
    width: 25%;
    padding: 0;
    border-bottom: none;
  }

  @media (min-width: @desktop-width) {
    width: 202px;
  }
}

.header__toggle {
  width: 21px;
  height: 21px;
  .mp-reset();
  border: none;
  background-color: transparent;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.header__menu-menu,
.header__toggle--open .header__menu-cross {
  display: block;
}

.header__menu-cross,
.header__toggle--open .header__menu-menu {
  display: none;
}

.header__toggle--noevt {
  pointer-events: none;
}
