// input-custom

.input-custom {
  label {
    word-break: break-all;
  }

  input:checked ~ label::after {
    display: block;
  }

  input:hover ~ label::before {
    border-color: @basic-neutral;
  }

  input:active ~ label::before,
  input:active ~ label::after,
  input:disabled ~ label::before,
  input:disabled ~ label::after {
    opacity: 0.3;
  }
}
