// additional

.additional {
  padding-top: 28px;
  padding-bottom: 31px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
    padding-bottom: 75px;
    line-height: 1.25;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    .centered();
    padding-top: 8px;
    padding-right: 111px;
    padding-bottom: 87px;
    padding-left: 110px;
  }
}

.additional__title {
  padding-right: 20px;
  padding-left: 20px;
  font-size: 1.714em;
  line-height: 1;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    padding-right: 30px;
    padding-left: 67px;
    font-size: 2.25em;
  }

  @media (min-width: @desktop-width) {
    padding-left: 0;
  }
}

.additional__grid {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 80px;
  }
}

.additional__content {
  .mp-reset();
  padding-top: 34px;
  padding-right: 20px;
  padding-left: 20px;
  list-style: none;

  @media (min-width: @tablet-width) {
    padding-top: 80px;
    padding-right: 30px;
    padding-left: 30px;
  }

  @media (min-width: @desktop-width) {
    width: 73.5%;
    padding: 0;
  }
}
