// pet-info

.pet-info {
  .mp-reset();
  padding-right: 20px;
  padding-bottom: 14px;
  padding-left: 19px;
  text-transform: uppercase;
  border: none;

  @media (min-width: @tablet-width) {
    padding-right: 66px;
    padding-bottom: 53px;
    padding-left: 71px;
  }

  @media (min-width: @desktop-width) {
    width: 46.8%;
    padding: 0;
  }
}

.pet-info__holder {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 14px;
  align-items: center;

  label {
    padding-right: 15px;
  }

  input {
    flex-grow: 1;
    padding-top: 13px;
    padding-right: 16px;
    padding-bottom: 12px;
    padding-left: 16px;
    text-transform: uppercase;
    border: 1px solid @page-darken-gray;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 21px;

    label {
      padding-right: 18px;
    }

    input {
      padding-top: 9px;
      padding-right: 16px;
      padding-bottom: 10px;
      padding-left: 18px;
    }
  }
}
