// input-states

.input-states {
  label {
    word-break: break-all;
  }

  input:hover {
    border-color: @basic-neutral;
  }

  input:active {
    border-color: @basic-black;
  }

  input:focus {
    outline: 2px solid @basic-black;
  }

  input:focus:valid {
    outline: 2px solid @page-green;
  }

  input:focus:invalid {
    outline: 2px solid tomato;
  }
}
