// page-title
.page-title {
  min-height: 72px;
  margin-bottom: 26px;
  font-size: 2.571em;
  font-weight: 400;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 60%;
    margin-left: 88px;
    margin-bottom: 42px;
    font-size: 3.75em;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    width: 35%;
    margin-left: 80px;
  }
}
