// footer

.footer {
  font-size: 1em;
  line-height: 1;
  background-color: @page-gray;
}

.footer__content {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 38px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 30px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: @basic-light;
  }

  &::before {
    top: 92px;
  }

  &::after {
    top: 155px;
  }

  @media (min-width: @tablet-width) {
    flex-direction: row;
    padding-top: 54px;
    padding-right: 30px;
    padding-bottom: 62px;
    padding-left: 30px;

    &::before,
    &::after {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    .centered();
    padding-right: 110px;
    padding-left: 110px;
  }
}

.footer__logo {
  padding-bottom: 32px;

  @media (min-width: @tablet-width) {
    padding: 0;
  }
}

.footer__social {
  padding-top: 20px;
  padding-bottom: 22px;

  @media (min-width: @tablet-width) {
    margin-left: auto;
    margin-right: 17%;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    margin-right: 31%;
  }
}

.footer__copy {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-top: 21px;
  font-family: "Arial", sans-serif;

  @media (min-width: @tablet-width) {
    width: 23.2%;
    max-width: 225px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    width: 164px;
    padding: 0;
  }
}

.footer__copy-link {
  &:hover {
    fill: @page-green;
  }

  &:active {
    opacity: 0.6;
  }
}

.footer__copy-icon {
  background-image: url("../img/icon-academy.svg");
  background-size: 27px 34px;
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }

  @media (min-width: @tablet-width) {
    width: 27px;
    height: 34px;

    background-size: 27px 34px;
  }
}
