// pet-diet

.pet-diet {
  .mp-reset();
  padding-top: 21px;
  padding-right: 20px;
  padding-left: 20px;
  text-transform: uppercase;
  border: 1px solid @page-darken-gray;

  @media (min-width: @tablet-width) {
    margin-right: 66px;
    margin-left: 71px;
    padding-top: 33px;
    padding-right: 28px;
    padding-left: 28px;
  }

  @media (min-width: @desktop-width) {
    width: 46.8%;
    margin: 0;
  }
}

.pet-diet__holder {
  margin-bottom: 25px;

  label {
    position: relative;
    padding-left: 45px;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: -3px;
      width: 30px;
      height: 30px;
      border: 1px solid @page-darken-gray;
      border-radius: 50%;
    }

    &::after {
      content: "";
      position: absolute;
      display: none;
      left: 8px;
      top: 5px;
      width: 14px;
      height: 14px;
      background-color: @page-green;
      border-radius: 50%;
    }
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 33px;

    label {
      padding-left: 58px;

      &::before {
        top: -5px;
        left: -1px;
        width: 40px;
        height: 40px;
      }

      &::after {
        width: 18px;
        height: 18px;
        top: 6px;
        left: 10px;
      }
    }
  }
}
