// contacts

.contacts {
  line-height: 1.45;

  &--inner {
    background-color: @page-gray;
  }

  @media (min-width: @tablet-width) {
    line-height: 1.25;
  }
}

.contacts__positioner {
  @media (min-width: @desktop-width) {
    position: relative;
    width: 1440px;
    .centered();
  }
}

.contacts__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 32px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 30px;

  @media (min-width: @tablet-width) {
    padding-top: 67px;
    padding-right: 72px;
    padding-left: 71px;
    padding-bottom: 60px;
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    z-index: 55;
    top: 90px;
    left: 110px;
    width: 570px;
    height: 201px;
    padding-top: 69px;
    padding-right: 84px;
    padding-left: 77px;
    background-color: @basic-white;
  }
}

.contacts__proposal {
  width: 140px;
  font-size: 1.143em;
  line-height: 1.25;
  text-transform: uppercase;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    width: 160px;
    font-size: 1.25em;
  }
}

.contacts__address {
  width: 140px;
  font-family: "Arial", sans-serif;
  font-style: normal;

  @media (min-width: @tablet-width) {
    width: 160px;
    line-height: 1.1;
    text-align: right;
  }

  @media (min-width: @desktop-width) {
    text-align: left;
  }
}

.contacts__city {
  @media (min-width: @tablet-width) {
    display: block;
    padding-top: 24px;
  }
}
