// owner-additional

.owner-additional {
  .mp-reset();
  padding-right: 20px;
  padding-left: 17px;
  border: none;

  @media (min-width: @tablet-width) {
    padding-right: 65px;
    padding-left: 69px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.owner-additional__title {
  width: 188px;
  margin-bottom: 41px;

  @media (min-width: @tablet-width) {
    width: 292px;
    margin-bottom: 66px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 69px;
  }
}

.owner-additional__content {
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.owner-additional__holder {
  margin-bottom: 24px;

  label {
    position: relative;
    padding-left: 49px;


    &::before {
      content: "";
      position: absolute;
      top: -3px;
      left: 3px;
      width: 30px;
      height: 30px;
      border: 1px solid @page-darken-gray;
    }

    &::after {
      content: "";
      position: absolute;
      top: 1px;
      left: 8px;
      display: none;
      width: 21px;
      height: 13px;
      border: 2px solid @page-green;
      border-top-style: none;
      border-right-style: none;
      transform: rotate(-50deg);
    }
  }

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 45.7%;

    label {
      margin-bottom: 28px;
      padding-left: 59px;

      &::before {
        width: 40px;
        height: 40px;
        top: -10px;
        left: 2px;
      }

      &::after {
        top: 1px;
        left: 11px;
      }
    }
  }

  @media (min-width: @desktop-width) {
    width: 20.1%;

    label {
      padding-left: 58px;

      &::before {
        left: 0;
      }

      &::after {
        top: 0;
        left: 10px;
      }
    }
  }
}
