// categories-card

.card {
  padding: 20px;
  padding-top: 29px;
  background-color: @page-gray;

  @media (min-width: @tablet-width) {
    padding-top: 76px;
    padding-left: 52px;
    padding-bottom: 55px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 77px;
    padding-right: 52px;
    padding-left: 53px;
  }
}

.card__content {
  @media (min-width: @tablet-width) {
    width: 60%;
  }

  @media (min-width: @desktop-width) {
    width: auto;
  }
}

.card__title {
  position: relative;
  min-height: 50px;
  margin-bottom: 17px;
  padding-left: 71px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 1.714em;
  line-height: 1.54;
  font-weight: 400;
  text-transform: uppercase;

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    left: 2px;
    width: 50px;
    height: 50px;
    background-color: @page-green;
    border-radius: 50%;
  }

  &--slim {
    &::after {
      content: "";
      position: absolute;
      top: -8px;
      left: 10px;
      width: 35px;
      height: 49px;
      background-image: url("../img/icon-cat-slim.svg");
      background-repeat: no-repeat;
      background-size: 100%;
    }
  }

  &--pro {
    &::after {
      content: "";
      position: absolute;
      top: 5px;
      left: -7px;
      width: 67px;
      height: 28px;
      background-image: url("../img/icon-cat-pro.svg");
      background-repeat: no-repeat;
      background-size: 100%;
    }
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 12px;
    padding-left: 0;
    font-size: 2.25em;
    line-height: 1.02;

    &::before {
      top: 16px;
      left: auto;
      right: -212px;
      width: 200px;
      height: 200px;
    }

    &--slim {
      &::after {
        top: 4px;
        left: auto;
        right: -182px;
        width: 140px;
        height: 194px;
      }
    }

    &--pro {
      &::after {
        top: 59px;
        left: auto;
        right: -246px;
        width: 268px;
        height: 112px;
      }
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 62px;
    padding-left: 162px;

    &::before {
      top: -29px;
      left: 0;
      right: auto;
      width: 100px;
      height: 100px;
    }

    &--slim {
      &::after {
        top: -36px;
        left: 13px;
        right: auto;
        width: 70px;
        height: 97px;
      }
    }

    &--pro {
      &::after {
        top: -7px;
        left: -18px;
        right: auto;
        width: 134px;
        height: 56px;
      }
    }
  }
}

.card__desc {
  margin-bottom: 17px;
  padding-bottom: 23px;
  border-bottom: 2px solid @basic-light;

  @media (min-width: @tablet-width) {
    min-height: 119px;
    margin-bottom: 33px;
    border-bottom: none;
  }

  @media (min-width: @desktop-width) {
    min-height: 102px;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.card__to-catalog {
  position: relative;
  padding-right: 35px;
  display: block;
  width: 130px;
  // width: 97px;
  min-width: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 1.143em;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  color: @page-black;

  &::before {
    top: 5px;
    left: 75%;
  }

  @media (min-width: @tablet-width) {
    width: 170px;
    padding-right: 48px;
    font-size: 1.25em;

    &::before {
      top: 9px;
    }
  }
}
