// navigation

.nav {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 62.2%;
    max-width: 480px;
  }

  @media (min-width: @desktop-width) {
    width: 39.3%;

    &--inner .nav__link {
      color: @page-black;
    }

    &--inner .nav__link--current {
      border-bottom: 2px solid @page-green;
    }
  }
}

.nav--closed {
  display: none;
}

.nav--nojs {
  display: flex;
}

.nav__link {
  display: block;
  width: 100%;
  min-width: 0;
  padding-top: 23px;
  padding-right: 20px;
  padding-bottom: 22px;
  padding-left: 20px;
  color: @page-black;
  font-size: 1.429em;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  overflow: hidden;
  border-bottom: 1px solid @page-dark-gray;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    width: auto;
    padding: 0;
    padding-top: 17px;
    padding-bottom: 11px;
    font-size: 1.125em;
    font-weight: 400;
    border-bottom: none;
  }

  @media (min-width: @desktop-width) {
    color: @basic-white;
  }
}

.nav__link--current {
  border-bottom: 1px solid @page-green;

  @media (min-width: @tablet-width) {
    border-bottom: 2px solid @page-green;
  }

  @media (min-width: @desktop-width) {
    border-bottom-color: @basic-white;
  }
}
