// extra-card

.extra-card {
  padding-top: 13px;
  padding-bottom: 16px;
  border-top: 1px solid @page-dark-gray;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 26px;
    padding-right: 36px;
    padding-bottom: 26px;
    padding-left: 37px;
    border: 1px solid @page-dark-gray;
    border-bottom-style: none;
  }

  @media (min-width: @desktop-width) {
    align-items: center;
    width: auto;
    padding-top: 15px;
    padding-right: 0;
    padding-bottom: 16px;
    padding-left: 0;
    border-right-style: none;
    border-left-style: none;
  }
}

.extra-card:last-child {
  border-bottom-style: 1px solid @page-dark-gray;
}

.extra-card__title {
  margin-bottom: 13px;
  font-size: 1.143em;
  line-height: 1;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    width: 64%;
    margin-bottom: 26px;
    font-size: 1.25em;
  }

  @media (min-width: @desktop-width) {
    width: 30%;
    margin-bottom: 0;
  }
}

// table
.extra-card__table {
  width: 100%;
  margin-bottom: 13px;
  font-family: "Arial", sans-serif;
  border-collapse: collapse;
  table-layout: fixed;

  @media (min-width: @tablet-width) {
    width: 36%;
  }

  @media (min-width: @desktop-width) {
    width: 30%;
    margin-bottom: 0;
  }
}

.extra-card__name {
  padding: 0;

  @media (min-width: @tablet-width) {
    padding-top: 3px;
    text-align: right;
  }

  @media (min-width: @desktop-width) {
    text-align: left;
    padding-top: 7px;
  }
}

.extra-card__value {
  padding: 0;
  text-align: right;

  @media (min-width: @tablet-width) {
    width: 42%;
    padding-top: 3px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 7px;
  }
}

.extra-card__order-button {
  width: 100%;

  @media (min-width: @desktop-width) {
    width: 27.4%;
  }
}
