// advantages

.advantages {
  @media (min-width: @desktop-width) {
    width: 1440px;
    .centered();
  }
}

.advantages__content {
  padding-right: 20px;
  padding-bottom: 42px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    padding-top: 15px;
    padding-right: 30px;
    padding-bottom: 60px;
    padding-left: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 43px;
    padding-right: 110px;
    padding-left: 110px;
    padding-bottom: 30px;
  }
}

.advantages__title {
  margin-bottom: 38px;
  font-size: 2.571em;
  line-height: 1.4;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    margin-bottom: 71px;
    font-size: 3.75em;
    line-height: 1;
  }
}

.advantages__list {
  .mp-reset();
  font-family: "Arial", sans-serif;
  list-style: none;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 89%;
  }

  @media (min-width: @desktop-width) {
    width: auto;
  }
}

.advantages__item {
  position: relative;
  min-height: 55px;
  margin-bottom: 25px;
  padding-left: 80px;

  &::before {
    content: "";
    position: absolute;
    top: -4px;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: @page-green;
    background-image: url("../img/advantage-1.png");
    background-repeat: no-repeat;
    background-size: 25px 28px;
    background-position: center;
  }

  &--2 {
    &::before {
      background-image: url("../img/advantage-2.png");
    }
  }

  &--3 {
    &::before {
      background-image: url("../img/advantage-3.png");
    }
  }

  &--4 {
    &::before {
      background-image: url("../img/advantage-4.png");
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    width: 39%;
    min-height: 227px;
    margin-bottom: 90px;
    padding-left: 0;
    padding-top: 113px;

    &::before {
      top: 0;
      width: 80px;
      height: 80px;
    }

    &::after {
      content: "1";
      position: absolute;
      z-index: -1;
      top: -39px;
      right: 18px;
      font-family: "Oswald", "Arial", sans-serif;
      font-size: 281px;
      line-height: 1;
      color: @page-dark-gray;
    }

    &--2 {
      &::after {
        content: "2";
        right: 0;
      }
    }

    &--3 {
      margin-bottom: 0;

      &::after {
        content: "3";
        right: 0;
      }
    }

    &--4 {
      &::after {
        content: "4";
        right: 0;
      }
    }
  }

  @media (min-width: @desktop-width) {
    width: 20.2%;
    margin-bottom: 0;
  }
}
