// button

.button {
  display: block;
  min-width: 169px;
  min-height: 40px;
  .mp-reset();
  padding: 10px;
  color: @basic-white;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  border: none;
  background-color: @page-green;

  &:hover {
    background-color: @page-green-hover;
  }

  &:active {
    opacity: 0.3;
  }
}
