// section-decoration

.section-decoration {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 15px;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: @page-green;
  }

  @media (min-width: @tablet-width) {
    &::before {
      top: 21px;
    }
  }

  @media (min-width: @desktop-width) {
    &::before {
      top: 23px;
      width: 118%;
      left: -110px;
    }
  }
}

.section-decoration__title {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: -20px;
    width: 100%;
    height: 100%;
    // background-color: violet;
    background-color: @basic-white;
  }

  @media (min-width: @tablet-width) {
    &::before {
      left: -40px;
    }
  }
}
