// promo

.extra-promo {
  padding-top: 40px;
  padding-right: 20px;
  padding-left: 20px;
  font-family: "Arial", sans-serif;
  font-size: 1.143em;
  line-height: 1.25;
  color: @basic-white;

  @media (min-width: @tablet-width) {
    padding-top: 70px;
    padding-right: 30px;
    padding-left: 30px;
    font-size: 1em;
  }

  @media (min-width: @desktop-width) {
    width: 20%;
    padding: 0;
  }
}

.extra-promo__content {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  min-height: 289px;
  padding-top: 135px;
  padding-bottom: 41px;
  background-color: @page-green;
  background-image: url("../img/gift-photo-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &::before {
    content: "";
    position: absolute;
    top: 66px;
    left: calc(50% - 55px / 2);
    width: 55px;
    height: 60px;
    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    background-size: 55px 60px;
    background-position: center;
  }

  @media (min-width: @tablet-width) {
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: row;
    min-height: 200px;
    padding-top: 70px;
    padding-bottom: 0;
    padding-right: 120px;
    background-image: url("../img/gift-photo-tablet.jpg");

    &::before {
      left: 90px;
    }
  }

  @media (min-width: @desktop-width) {
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    min-height: 289px;
    padding-top: 135px;
    padding-right: 0;
    padding-bottom: 41px;
    background-image: url("../img/gift-photo-desktop.jpg");
    background-size: 245px 289px;
    background-position: center;
  }
}

.extra-promo__desc {
  width: 60%;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 28%;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    width: 70%;
    text-align: center;
  }
}
