// compare

.compare {
  line-height: 1.29;
  background-color: @page-gray;
  border-top: 1px solid @page-dark-gray;
  border-bottom: 1px solid @page-dark-gray;

  @media (min-width: @tablet-width) {
    position: relative;
    line-height: 1.5;
    background-color: transparent;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 42%;
      background-color: @page-gray;
    }
  }

  @media (min-width: @desktop-width) {
    border-top: none;

    &::before {
      height: 72.5%;
      z-index: -1;
    }
  }
}

.compare__content {
  padding-top: 20px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 32px;

  @media (min-width: @tablet-width) {
    padding-top: 43px;
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 72px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 1440px;
    .centered();
    padding-top: 0;
    padding-right: 41px;
    padding-bottom: 18px;
    padding-left: 110px;
  }
}

.compare__title {
  margin-bottom: 27px;
  font-size: 2.571em;
  line-height: 1.55;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    margin-bottom: 65px;
    font-size: 3.75em;
    line-height: 1;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 147px;
    padding-top: 54px;
  }
}

.compare__desc {
  margin-bottom: 25px;
  font-family: "Arial", sans-serif;

  @media (min-width: @tablet-width) {
    margin-bottom: 52px;
  }

  @media (min-width: @desktop-width) {
    width: 435px;
    margin-bottom: 68px;
  }
}

.compare__stat {
  margin-bottom: 40px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 52px;
  }

  @media (min-width: @desktop-width) {
    justify-content: flex-start;
    flex-direction: column;
    width: 435px;
  }
}

.compare__table {
  width: 100%;
  margin-bottom: 34px;
  border-collapse: collapse;

  tbody {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  tr {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 44%;
    min-height: 56px;
    .mp-reset();
    padding-top: 12px;
    border: 1px solid #cdcdcd;
    border-radius: 3px;
  }

  td {
    position: relative;
    z-index: 2;
    .mp-reset();
    font-size: 1.715em;
    line-height: 1;
    text-transform: uppercase;
  }

  th {
    position: absolute;
    // left: 15%;
    bottom: -16px;
    width: 70%;
    min-height: 25px;
    .mp-reset();
    padding-right: 12px;
    padding-left: 12px;
    font-family: "Arial", sans-serif;
    font-size: 0.857em;
    line-height: 1;
    font-weight: 400;
    background-color: @page-gray;
  }

  @media (min-width: @tablet-width) {
    width: 51.8%;

    tr {
      min-height: 72px;
      padding-top: 15px;
    }

    td {
      font-size: 1.875em;
      line-height: 1.23;
    }

    th {
      left: 10%;
      bottom: -17px;
      width: 80%;
      font-size: 0.875em;
      background-color: @basic-white;
    }
  }

  @media (min-width: @desktop-width) {
    width: 367px;
    margin-bottom: 51px;

    th {
      background-color: @page-gray;
    }
  }
}

.compare__sum {
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 25%;
    text-align: right;
    font-size: 1.25em;
    line-height: 1.7;
  }

  @media (min-width: @desktop-width) {
    width: auto;
    text-align: left;
  }
}

.compare__value {
  @media (min-width: @desktop-width) {
    padding-left: 60px;
  }
}

.compare__slider {
  width: 280px;
  .centered();

  @media (min-width: @tablet-width) {
    width: 690px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 0;
    margin-left: 0;
  }
}

.compare__images {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 250px;
  margin-bottom: 17px;
  overflow: hidden;

  @media (min-width: @tablet-width) {
    min-height: 499px;
    margin-bottom: 38px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 58px;
  }
}

.compare__image {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: auto;
  height: auto;

  img {
    max-width: none;
  }

  &--before {
    z-index: 2;
    top: 9px;
    left: -22px;
    width: 61%;
    background-color: @page-gray;
  }

  &--after {
    left: 30px;
  }

  @media (min-width: @tablet-width) {
    left: 20px;

    img {
      width: 677px;
      height: 499px;
    }

    &--before {
      top: 0;
      left: 10px;
      background-color: transparent;

      &::before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        width: 100%;
        height: 33%;
        background-color: @basic-white;
      }
    }
  }
}

.compare__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 71%;
  .centered();

  @media (min-width: @tablet-width) {
    width: 80%;
  }
}

.compare__text {
  color: @page-black;
  font-size: 1.143em;
  line-height: 1.25;
  text-transform: uppercase;
  text-decoration: none;

  @media (min-width: @tablet-width) {
    position: relative;
    font-size: 1.25em;
    line-height: 1;
    text-decoration: underline dotted;
    text-decoration-color: @page-black;
  }
}

.compare__controls-holder {
  position: relative;
  width: 42%;
  min-height: 14px;

  @media (min-width: @tablet-width) {
    width: 80%;
    min-height: auto;
  }
}

.compare__range {
  height: 14px;
  background-color: @basic-white;
  border: 1px solid #cdcdcd;
  border-radius: 5px;

  @media (min-width: @tablet-width) {
    width: 100%;
    height: 4px;
    background-color: #cdcdcd;
    border: none;
  }
}

.compare__progress-bar {
  position: absolute;
  left: 0;
  top: 4px;
  width: 50%;
  height: 6px;
  background-color: @page-green;
  border-radius: 5px;

  @media (min-width: @tablet-width) {
    top: 0;
    height: 5px;
  }
}

.compare__slide-button {
  position: absolute;
  z-index: 505;
  top: calc(50% - 3px);
  left: 50%;
  width: 6px;
  height: 6px;
  background-color: @page-green;
  border-radius: 5px;
  cursor: w-resize;

  @media (min-width: @tablet-width) {
    top: -15px;
    left: calc(50% - 34px / 2);
    width: 34px;
    height: 34px;
    background-color: @basic-white;
    border: 1px solid #cdcdcd;
    border-radius: 50%;
    box-shadow: 0 1px 2px 2px @page-dark-gray;

    &::before {
      content: "";
      position: absolute;
      top: calc(50% - 4px);
      left: calc(50% - 4px);
      width: 8px;
      height: 8px;
      background-color: @page-green;
      border-radius: 50%;
      transition-property: all;
      transition-duration: 0.2s;
      transition-timing-function: linear;
    }
  }

  &:hover,
  &:hover::before {
    transform: scale(1.05);
  }
}
