// food-card

.food-card {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-right: 19px;
  padding-bottom: 24px;
  padding-left: 19px;
  border: 1px solid @page-dark-gray;
  border-bottom-style: none;

  @media (min-width: @tablet-width) {
    flex-direction: column;
    align-items: center;
    min-height: 510px;
    padding-right: 37px;
    padding-bottom: 41px;
    padding-left: 38px;
    background-image: linear-gradient(
      to bottom,
      @basic-white 15%,
      @page-gray 15%
    );
    border: none;
  }

  @media (min-width: @desktop-width) {
    max-width: 245px;
    margin-bottom: 65px;
  }
}

.food-card--small {
  @media (min-width: @tablet-width) {
    padding-top: 32px;

    .food-card__img {
      width: 100%;
      height: 172px;
    }
  }
}

.food-card--more {
  padding-top: 26px;
  border-bottom: 2px solid @page-dark-gray;

  .food-card__desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .food-card__more-button {
    position: relative;
    display: block;
    width: 30px;
    height: 30px;
    margin-bottom: 23px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 14px;
      left: calc(50% - 100% / 2);
      width: 100%;
      height: 2px;
      background-color: @page-darken-gray;
    }

    &::after {
      transform: rotate(90deg);
    }
  }

  .food-card__title {
    width: auto;
    margin-bottom: 9px;
  }

  .food-card__more-info {
    margin-bottom: 20px;
    font-family: "Arial", sans-serif;
    font-size: 0.857em;
    text-align: center;
  }

  .food-card__order-button {
    color: @basic-black;
    background-color: @page-dark-gray;

    &:hover {
      background-color: @page-darken-gray;
    }
  }

  @media (min-width: @tablet-width) {
    position: relative;
    padding-top: 130px;
    background-image: none;
    border: 1px solid @page-dark-gray;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: -1px;
      left: -1px;
      width: 101%;
      height: 75px;
      background-color: @basic-white;
    }

    &::after {
      top: 74px;
      width: 100%;
      height: 2px;
      background-color: @page-dark-gray;
    }

    .food-card__more-button {
      width: 60px;
      height: 60px;
      margin-bottom: 48px;

      &::before,
      &::after {
        top: 28px;
      }
    }

    .food-card__title {
      width: 60%;
      margin-bottom: 25px;
    }

    .food-card__more-info {
      width: 70%;
      font-size: 1em;
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 130px;

    .food-card__title {
      width: 80%;
    }

    .food-card__more-info {
      width: 100%;
    }
  }
}

.food-card__img {
  width: 100%;

  @media (min-width: @tablet-width) {
    width: 195px;
    height: 205px;
  }

  @media (min-width: @desktop-width) {
    width: 230px;
  }
}

.food-card__desc {
  width: 50%;
  align-self: center;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
  }
}

.food-card__title {
  width: 55%;
  margin-bottom: 15px;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    align-self: center;
    font-size: 1.25em;
    line-height: 1;
    text-align: center;
    font-weight: 400;
  }

  @media (min-width: @desktop-width) {
    width: 80%;
    margin-bottom: 18px;
  }
}

// table

.food-card__spec {
  width: 100%;
  font-size: 0.857em;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  table-layout: fixed;
  border-collapse: collapse;

  @media (min-width: @tablet-width) {
    font-size: 1em;
    line-height: 1;

    tr {
      border-bottom: 2px solid @page-darken-gray;
    }
  }
}

.food-card__name {
  padding: 0;

  @media (min-width: @tablet-width) {
    padding-top: 10px;
    padding-bottom: 5px;
  }
}

.food-card__value {
  padding: 0;
  text-align: right;

  @media (min-width: @tablet-width) {
    padding-top: 10px;
    padding-bottom: 5px;
  }
}

.food-card__order-button {
  width: 100%;
}
