// promo

.promo {
  position: relative;
  color: @basic-white;
  line-height: 1;
  background-image: url("../img/index-background-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 0;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 306px;
    // height: 61%;
    background-color: @page-green;
  }

  @media (min-width: @tablet-width) {
    color: @page-black;
    background-image: none;

    &::before {
      display: none;
    }
  }
}

.promo__content {
  padding-top: 27px;
  padding-bottom: 22px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding-right: 30px;
    padding-bottom: 0;
    padding-left: 30px;

    picture {
      order: 1;
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 80px;
    padding-right: 110px;
    padding-bottom: 55px;
    padding-left: 110px;
  }
}

.promo__slogan {
  z-index: 595;
  margin-bottom: 27px;
  font-size: 1em;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 60%;
    margin-left: 88px;
    margin-bottom: 48px;
    font-size: 1.25em;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    margin-left: 80px;
    margin-bottom: 51px;
  }
}

.promo__image {
  width: 89%;
  .centered();

  @media (min-width: @tablet-width) {
    width: 709px;
    height: 609px;
    margin-top: -80px;
  }

  @media (min-width: @desktop-width) {
    width: 552px;
    height: 499px;
    margin-top: -365px;
    margin-right: 0;
    margin-left: 37%;
  }
}

.promo__button {
  width: 88%;
  .centered();
  line-height: 1.28;

  @media (min-width: @tablet-width) {
    z-index: 2;
    width: 245px;
    min-height: 52px;
    margin: 0;
    margin-left: 88px;
    padding-top: 13px;
    font-size: 1.25em;
  }

  @media (min-width: @desktop-width) {
    margin-left: 80px;
  }
}
