// owner-contacts

.owner-contacts {
  .mp-reset();
  padding-right: 20px;
  padding-bottom: 14px;
  padding-left: 19px;
  border: none;

  @media (min-width: @tablet-width) {
    padding-right: 65px;
    padding-bottom: 48px;
    padding-left: 69px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 0;
    padding-right: 0;
  }
}

.owner-contacts__title {
  width: 223px;
  .mp-reset();
  margin-bottom: 34px;

  @media (min-width: @tablet-width) {
    width: 590px;
    margin-bottom: 75px;
  }
}

.owner-contacts__content {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.owner-contacts__holder {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-transform: uppercase;
  margin-bottom: 14px;

  label {
    padding-right: 12px;
  }

  input {
    flex-grow: 1;
    padding-top: 13px;
    padding-right: 33px;
    padding-bottom: 12px;
    padding-left: 14px;
    text-transform: uppercase;
    border: 1px solid @page-darken-gray;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 20px;

    label {
      padding-right: 18px;
    }

    input {
      padding-top: 9px;
      padding-right: 50px;
      padding-bottom: 10px;
      padding-left: 21px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 46.8%;

    input {
      padding-top: 10px;
      padding-left: 17px;
    }
  }
}
