// logo

.logo {
  img {
    width: 100%;
  }

  width: 191px;
  height: 38px;

  @media (min-width: @tablet-width) {
    width: 173px;
    height: 58px;
  }

  @media (min-width: @desktop-width) {
    width: 202px;
    height: 59px;
  }
}

.logo--inner {
  display: block;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}
