// owner-commentary

.owner-commentary {
  .mp-reset();
  padding-right: 20px;
  padding-bottom: 25px;
  padding-left: 17px;
  border: none;

  @media (min-width: @tablet-width) {
    padding-right: 65px;
    padding-bottom: 63px;
    padding-left: 69px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
    padding-bottom: 63px;
  }
}

.owner-commentary__title {
  width: 165px;
  margin-bottom: 36px;

  @media (min-width: @tablet-width) {
    width: 260px;
    margin-bottom: 77px;
  }
}

.owner-commentary__text {
  width: 100%;
  min-height: 150px;
  padding-top: 10px;
  padding-right: 12px;
  padding-bottom: 10px;
  padding-left: 17px;
  text-transform: uppercase;
  border: 1px solid @page-darken-gray;
  resize: none;

  @media (min-width: @tablet-width) {
    min-height: 174px;
    padding-top: 14px;
    padding-left: 19px;
  }
}
