// form

.programm {
  padding-top: 25px;
  padding-bottom: 29px;
  line-height: 1.125;

  @media (min-width: @tablet-width) {
    padding-top: 20px;
    padding-bottom: 70px;
    line-height: 1.25;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    padding-top: 38px;
    padding-bottom: 83px;
    .centered();
  }
}

.programm__title {
  min-height: auto;
  margin-bottom: 36px;
  padding-right: 20px;
  padding-left: 20px;
  text-align: left;

  @media (min-width: @tablet-width) {
    width: auto;
    margin-bottom: 49px;
    margin-left: 0;
    padding-left: 29px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 48px;
    padding-left: 30px;
  }
}

.programm__slogan {
  width: 85%;
  min-height: 34px;
  padding-right: 20px;
  padding-left: 20px;
  font-size: 1em;
  line-height: 1.28;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    width: 60%;
    padding-left: 70px;
    font-size: 1.25em;
    line-height: 1.5;
  }

  @media (min-width: @desktop-width) {
    width: 35%;
    padding-left: 111px;
  }
}

.programm__form {
  padding-top: 40px;
  font-size: 1.143em;

  @media (min-width: @tablet-width) {
    padding-top: 65px;
    font-size: 1.25em;
    line-height: 1;
  }

  @media (min-width: @desktop-width) {
    padding-top: 66px;
    padding-right: 110px;
    padding-left: 111px;
  }
}

.programm__base-info {
  @media (min-width: @tablet-width) {
    padding-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 49px;
  }
}

.programm__owner-info {
  padding-top: 29px;
  padding-bottom: 18px;

  @media (min-width: @tablet-width) {
    padding-bottom: 14px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 0;
    padding-bottom: 20px;
  }
}

.programm__submit {
  .mp-reset();
  padding-right: 20px;
  padding-left: 19px;
  border: none;

  @media (min-width: @tablet-width) {
    padding-right: 65px;
    padding-left: 71px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.programm__submit-button {
  width: 100%;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
    display: inline-block;
    width: 44.6%;
    min-height: 52px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    width: 46.6%;
  }
}

.programm__requred {
  width: 100%;
  font-family: "Arial", sans-serif;
  font-size: 0.875em;
  text-align: center;

  @media (min-width: @tablet-width) {
    display: inline-block;
    width: 46.5%;
    font-size: 0.8em;
    line-height: 1;
  }

  @media (min-width: @desktop-width) {
    width: 27.5%;
  }
}
