@font-face {
  font-family: "Oswald";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("oswaldregular"), url("../fonts/oswaldregular.woff2") format("woff2"), local("opensans"), url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-display: swap;
  font-style: normal;
  font-weight: 500;
  src: local("oswaldmedium"), url("../fonts/oswaldmedium.woff2") format("woff2"), local("opensans"), url("../fonts/oswaldmedium.woff") format("woff");
}

/* Указываем box sizing */

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внешние отступы */

body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

/* Выставляем основные настройки по-умолчанию для body */

body {
  min-width: 320px;
  min-height: 100vh;
  overflow-y: scroll;
  font-family: "Oswald", "Arial", sans-serif;
  color: #111111;
  overflow-wrap: break-word;
}

/* Упрощаем работу с изображениями */

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-main {
  font-size: 0.875em;
  line-height: 1.28;
}

.button {
