}

.input-states input:active {
  border-color: #000000;
}

.input-states input:focus {
  outline: 2px solid #000000;
}

.input-states input:focus:valid {
  outline: 2px solid #68b738;
}

.input-states input:focus:invalid {
  outline: 2px solid tomato;
}

.input-custom label {
  word-break: break-all;
}

.input-custom input:checked ~ label::after {
  display: block;
}

.input-custom input:hover ~ label::before {
  border-color: #b1b3b6;
}

.input-custom input:active ~ label::before,
.input-custom input:active ~ label::after,
.input-custom input:disabled ~ label::before,
.input-custom input:disabled ~ label::after {
  opacity: 0.3;
}

