// catalog

.catalog {
  @media (min-width: @tablet-width) {
    padding-top: 20px;
    padding-right: 30px;
    padding-left: 30px;
    line-height: 1.25;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    .centered();
    padding-top: 38px;
    padding-right: 40px;
    padding-left: 40px;
  }
}

.catalog__title {
  margin-bottom: 0;
  padding-top: 22px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;

  @media (min-width: @tablet-width) {
    width: auto;
    margin: 0;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    margin-left: 70px;
  }
}

.catalog__content {
  .mp-reset();
  padding-top: 31px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 80px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 70px;
    padding-left: 70px;
    justify-content: flex-start;
  }
}

.catalog__food-card {
  @media (min-width: @tablet-width) {
    width: 45.9%;
    margin-bottom: 62px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 80px;

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
